@keyframes gradientAnimation {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}

.project-container {

  background: linear-gradient(135deg, #f3904f, #3b4371);
  background-size: 300% 300%;
  animation: gradientAnimation 12s ease infinite;
  min-height: 100vh;


}


.section-heading {
  font-size: 2.5rem;
  font-weight: bold;
  color: #fff;
  text-align: center;

  margin-bottom: 0px;
  padding-top: 100px;
  font-size: 3rem;
  color: #ffd700;
  font-weight: bold;

}

.section-subheading {
  font-size: 1.5rem;
  color: #fff;
  text-align: center;
  margin-bottom: 20px;
}









.project-section {
  max-width: 1200px;

  padding: 60px 20px;
  display: block;
  flex-wrap: wrap;
  align-content: center;
  gap: 40px;
  width: 100%;

  margin-top: -100px;


}

.project-content {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 40px;
  max-width: 1200px;
  margin: 50px auto;

  padding: 20px;
  border-radius: 10px;
  background: #3b2b2b56;
  text-align: left;
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.5);
  transition: transform 0.3s ease, box-shadow 0.3s ease, background 0.3s ease;
  justify-content: center;





}

.project-info {
  flex: 1;
  max-width: 500px;

}

.project-number-box {
  background: #003399;
  /* Accent color, can be changed */
  display: inline-block;
  padding: 5px 10px;
  margin-bottom: 10px;
  border-radius: 5px;


}

.project-number {
  font-size: 2.5rem;
  font-weight: bold;
  color: #fff;
}

.project-title {
  font-size: 2.5rem;
  font-weight: 600;
  color: #3399ff;
  /* Same or complementary accent color */
  margin: 10px 0;
}

.project-description {
  font-size: 1.1rem;
  color: #ccc;
  margin-bottom: 20px;
  line-height: 1.6;
}

.project-tech {
  display: inline-block;
  background: #1a1a1a;
  padding: 8px 12px;
  border-radius: 5px;
  margin-bottom: 20px;
  font-size: 1.2rem;
  color: #ccc;
}

.project-tech span {
  color: #fff;
}

.project-actions {
  display: flex;
  gap: 15px;
  margin-top: 20px;
}

.action-btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 45px;
  height: 45px;
  background: #ff0000;
  border-radius: 50%;
  color: #fff;
  font-size: 1.2rem;
  transition: background 0.3s ease;
  text-decoration: none;
}

A .action-btn:hover {
  background: #3399ff;
  /* Accent highlight on hover */
}


.project-image-wrapper {
  position: relative;
  flex: 1;
  max-width: 600px;
}

.project-image {
  width: 100%;
  border-radius: 10px;
  border: 2px solid #1a1a1a;
  object-fit: cover;
}

.project-section project-nav {
  position: fixed;
  margin-inline-start: 10px;
  bottom: 300px;
  right: 20px;
  display: flex;
  gap: 20px;


}

.nav-btn {
  background: #0044cc;
  color: #fff;
  border: none;
  padding: 8px 12px;
  border-radius: 50px;
  cursor: pointer;
  font-size: 1rem;
  transition: background 0.3s ease;
  margin-left: 10px;

}

.nav-btn:hover {
  background: #3399ff;
}

.project-video {
  width: 100%;
  max-height: 400px;
  border-radius: 10px;



}


/* Responsive Adjustments */
@media(max-width: 768px) {
  .project-content {
    flex-direction: column;
    text-align: center;
  }

  .project-info {
    max-width: 100%;
  }

  .project-video {
    width: 100%;
    max-height: 400px;
    border-radius: 30px;
    object-fit: cover;
    margin-top: 20px;

  }

  .project-video-wrapper {
    max-width: 100%;
    /* Allow full width on small screens */
    autoplay: false;
  }

  .project-nav {
    padding: 0 5px;
    margin-top: -50px;
  }

  .nav-btn {
    font-size: 0.8rem;
    padding: 8px 12px;
  }


  .nav-btn {
    font-size: 0.9rem;
    margin-left: 10px;
    margin-bottom: -10px;
    margin-block: 50px;


  }





}