@import url('https://fonts.googleapis.com/css2?family=Cherry+Cream+Soda&family=Montserrat+Underline:ital,wght@0,100..900;1,100..900&family=Nova+Flat&family=Yuji+Mai&display=swap');



.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  position: fixed;
  width: 100%;
  height: 90px;
  z-index: 1000;
  font-family: "Nova Flat", system-ui;
  font-weight: 400;
  font-style: normal;
  z-index: 1000;
}

header-bg {
  background-color: rgba(0, 0, 0, 0.9);
  transition: 0.5s;
}


.nav-menu {
  display: flex;
  font-size: large;


}

.nav-menu li {

  margin-right: 20px;
  padding: 0 1rem;

}

.nav-menu a {
  text-decoration: none;
  font-size: 1.2rem;
  font-weight: 500;
}

.burger {
  display: none;
}

.nav-menu :active {
  color: rgb(0, 13, 255);

}

.logo {
  font-family: "Nova Flat", system-ui;
  font-weight: 400;
  font-style: normal;
}

@media screen and (max-width: 768px) {


  .nav-menu {

    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    position: inherit;

    width: 50%;
    height: 45%;
    margin-left: 42%;
    margin-top: -5%;
    margin-bottom: 100%;

    background: #2c2b2bef;
    top: 10%;
    right: -100%;
    z-index: -3;
    transition-behavior: smooth;
    transition: 0.09s;

    border-bottom-left-radius: 10px;
    border-top-left-radius: 10px;
    border-bottom-right-radius: 10px;

  }

  .nav-menu:hover {
    left: 0;
    z-index: 10;
    color: blue;

  }

  .nav-menu.active {
    left: 0;
    z-index: 10;
    color: blue;

  }

  .nav-menu li {
    padding: 1rem 0;

  }

  ;


  .nav-menu li a {
    font-size: 1.5rem;
  }

  .burger {
    display: initial;
    cursor: pointer;
    z-index: 10;
    margin-top: 0;
  }

  .burger active {
    margin-top: 1rem;
  }
}