@keyframes gradientAnimation {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}


.about-section {
  padding: 2rem;
  background: linear-gradient(135deg, #ff416c, #f56b53, #1e90ff, #00bcd4);
  background-size: 300% 300%;
  animation: gradientAnimation 12s ease infinite;

  color: #e0e0e0;

}

.about-image {

  font-size: 2rem;
  font-weight: bold;

  text-align: center;
  margin-bottom: 20px;
  padding-top: 40px;
}

.about-image h1 {
  font-size: 3rem;
  color: #ffd700;
  font-weight: bold;

}

.about-intro {
  margin: 2rem auto;
}

h2 {
  color: #000000b8;
  text-align: center;
  margin-bottom: 1.5rem;
  font-size: 2.2rem;
  font-weight: bold;


}


.about-intro h3 {
  margin-bottom: 2rem;
  text-align: center;
  line-height: 1.8;
  color: #ffffff;
  background-color: #1e1e1ec8;
  padding: 1rem;
  border-radius: 12px;
}

.skills-grid,
.subjects-grid,
.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 1.5rem;
  margin: 2rem auto;
}

.skill,
.subject,
.stat {
  text-align: center;
  background: #121212c1;
  padding: 1rem;
  border-radius: 12px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.4);
  transition: transform 0.3s ease, box-shadow 0.3s ease, background 0.3s ease;
}

.skill:hover,
.subject:hover,
.stat:hover {
  transform: translateY(-5px);
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.7);
  background: #00bcd4;
  color: #121212;
}

.certifications-list {
  list-style: none;
  margin: 1rem auto;
  padding: 0;
  max-width: 800px;
}

.certification-item {
  background: #1e1e1ebf;
  margin-bottom: 0.7rem;
  padding: 1rem 1.5rem;
  border-radius: 8px;
  display: flex;
  align-items: center;
  gap: 0.8rem;
  color: #e0e0e0;
  font-weight: 600;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.5);
}

.certification-item:hover {
  background-color: #00bcd4;
  color: #121212;
}

.stats-section .stat {
  background: linear-gradient(to right, #00bcd4, #009688);
  color: #121212;
}

.stats-section h3 {
  font-size: 2.5rem;
  font-weight: bold;
}

.stats-section p {
  font-size: 1rem;
}